import './charList.scss';
import React,{ Component } from 'react';
import Spinner       from '../spinner/spinner';
import ErrorMessage  from '../errorMessage/errorMessage';
import MarvelService from '../../services/MarverServices';
import PropTypes     from 'prop-types'

class CharList extends Component {



    state = {
        charList:[],
        loading:true,
        error:false,
        newItemLoading:false,
        offset:210,
        charEnded:false,
    }

    marvelService = new MarvelService();


    
    componentDidMount() {
        this.onRequest();
    }


    onRequest = (offset) => {
        this.onCharListLoading();
        this.marvelService.getAllCharacters(offset)
        .then(this.onCharListLoaded)
        .catch(this.onError);
    }

    onCharListLoading = () =>{
        this.setState({
            newItemLoading:true,
        })
    }

   

    onCharListLoaded = (newcharList) => {

        let ended = false;
        if(newcharList.length < 9){
            ended = true;
        }

        this.setState(({offset,charList})=>({
            charList:[...charList, ...newcharList],
            loading:false,
            newItemLoading:false,
            offset: offset + 9,
            charEnded:ended,
           })
            
        );
    }

    onError = () => {
        this.setState(
            {
             loading:false,
             error:true,
            }
        );
    }
    

    updateChar = () => {
        const id = Math.floor(Math.random() * (1011400 - 1011000) + 1011000); //формула чтобы найти айди от меньшего до большего
        this.marvelService
            .getCharacter(id)
            .then(this.onCharLoaded)
            .catch(this.onError);
    }

    itemsRef = [];

    setRef = ref => {
        this.itemsRef.push(ref)
    }

    focusOnItem = (id) => {
        this.itemsRef.forEach(item => item.classList.remove('char__item_selected'));
        this.itemsRef[id].classList.add('char__item_selected');
        this.itemsRef[id].focus();
    }



    renderItems(arr) {
        const items =  arr.map((item,i) => {
            let imgStyle = {'objectFit' : 'cover'};
            if (item.thumbnail === 'http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg') {
                imgStyle = {'objectFit' : 'unset'};
            }
            
            return (
                <li  className="char__item"
                     key={item.id}
                     tabIndex={0}
                     ref={this.setRef}
                     onClick={()=>{
                        this.props.onCharSelected(item.id);
                        this.focusOnItem(i);
                     }}
                     onKeyPress={(e)=>{
                        if(e.key === ' ' || e.key === 'Enter'){
                            this.props.onCharSelected(item.id);
                            this.focusOnItem(i);
                        }
                     }}
                    >

                        <img src={item.thumbnail} alt={item.name} style={imgStyle}/>
                        <div className="char__name">{item.name}</div>
                </li>
                
            )
            
        });
        
        return (
            <ul className="char__grid">
                {items}
            </ul>
        )
    }
    
    



    render(){
    const {charList,loading,error,newItemLoading,offset,charEnded} = this.state;
    const items                    = this.renderItems(charList);
    const errorMessage             = error ? <ErrorMessage/> : null;
    const spinner                  = loading ? <Spinner/> : null;
    const content                  = !(loading || error) ?items  : null;


        return (
            <div className="char__list">
                    {errorMessage}
                    {spinner}
                    {content}
                <button 
                className="button button__main button__long"
                disabled={newItemLoading}
                style={{'display':charEnded ? 'none' : 'block'}}
                onClick={()=> this.onRequest(offset)}
                >
                    <div className="inner">load more</div>
                </button>
            </div>
        )
    }
}

CharList.propTypes = {
    onCharSelected:PropTypes.func.isRequired
}


export default CharList;
